*{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html,body{
  margin: 0;
  padding: 0;
  border: 0;
  background-color: #f5f5f5;
  /*font-family:proxima-nova,"Helvetica Neue",Helvetica,Roboto, PT Sans, DejaVu Sans, Arial,Segoe UI Light,Segoe UI,Microsoft Jhenghei,Mirco Yahei,'sans-serif';*/
  font-family:Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono';
}

img,ul,li{
  margin: 0;
  padding: 0;
  border: 0;
}

ul li{
  list-style:disc inside ;
}

a{
  text-decoration: none;
  color: #0366d6;
}

table{
  border-collapse: collapse;
}
table,td,th{
  border: 1px solid #ddd;
}

td,th{
  padding: 4px 8px;
}

em {
  color: rgb(255, 152, 0);
}

strong{
  color:rgb(244, 67, 54);
}

hr{
  margin-top: 1em;
  margin-bottom: 3em;
}

blockquote::before{
  content: '';
  position: absolute;
  left: 0;
  top: 0px;
  background-color: #dfe2e5;
  height: 24px;
  width: 4px;
}

blockquote {
  position: relative;
  margin: 0;
  padding-left: 20px;
}

pre[data-line] {
  position: relative;
  padding: 1em 0 1em 1em !important;
}

.line-highlight:before, .line-highlight[data-end]:after{
  display: none;
}

:not(pre) > code[class*="language-"], pre[class*="language-"] {
  font-size: 0.8em;
}

:not(pre) > code.language-markup{
  background-color: rgb(96, 125, 139);
  padding: 0.1em .3em;
}

.content img{
  max-width: 700px;
  border-radius: 5px;
}

@media only screen and (max-width: 768px) {
  html,body{
    overflow-x: hidden;
  }

  .content img{
      max-width: 100%;
  }


  .content code[class*="language-"],
  .content pre[class*="language-"] {
      font-family: Consolas, "Liberation Mono", Courier, monospace;
      line-height: 18px;
      font-size: 13px;
      margin:  0;
      width: 100%;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
  }

  .content pre[class*="language-"] {
      padding-left: 10px;
  }
  .content p{
      font-size: 14px;
  }
  .content p,.content h1,.content h2,.content h3,.content h4,.content h5{
      padding-left:10px;
      padding-right:10px;
  }

  .line-highlight:before, .line-highlight[data-end]:after{
      top: 0em;
  }
}

/********************************************************************/

.content li {
  padding-top: 3px;
  padding-bottom: 3px;
}


